<template>
  <div class="real-time-power">
    <SmallModule title="实时功率">
      <template #content>
        <div class="content-layout">
          <div style="transform: scale(0.85); transform-origin: 0 0">
            <EchartLineBarChart
              :data="activePowerChartData"
              :width="`26.14vw`"
              :height="`28.06vh`"
              :category="activePowerChartData.categories"
              :dataZoomMaxValueSpan="activePowerChartData.categories.length"
              ybgcolor="#01B7D730"
              xAxisLabelInterval="auto"
            />
          </div>
        </div>
      </template>
    </SmallModule>
  </div>
</template>

<script lang="ts" setup>
  import SmallModule from '/@/components/Framework/OperationScreen/SmallModule.vue';
  import EchartLineBarChart from '/@/components/Framework/Chart/EchartLineBarChart.vue';
  import { ref, reactive, UnwrapRef, onMounted } from 'vue';
  import { getActivePowerData } from './data';
  import { assign } from 'lodash-es';

  //搜索参数
  const searchInfo: UnwrapRef<any> = reactive({});
  interface Props {
    overviewSearchInfo?: {};
  }
  const props = withDefaults(defineProps<Props>(), {});
  onMounted(() => {
    assign(searchInfo, props.overviewSearchInfo);
    queryActivePowerData();
  });

  const activePowerChartData = ref({
    lineData: [],
    categories: [],
    dataZoomDefaultValueRange: [1, 2],
  });

  const queryActivePowerData = async () => {
    let data = await getActivePowerData(searchInfo);
    if (data.result) {
      activePowerChartData.value.lineData = data.result.lineData;
      activePowerChartData.value.categories = data.result.categories;
    }
  };
</script>

<style lang="less" scoped>
  .content-layout {
    height: 100%;
    padding: 0 0.14rem;
  }
</style>
